.main-footer {
  background-color: @basic-grey;
}

.main-footer__logo {
  padding: 39px 20px 32px;
  text-align: center;
  border-bottom: 1px @border-grey solid;
}

.main-footer__social-list {
  margin: 0;
  padding: 23px 20px;
  border-bottom: 1px @border-grey solid;
}

.main-footer__copyright {
  padding: 28px 20px 22px;
}

@media (min-width: @tablet-width) {
  .main-footer__under-wrapper {
    padding: 58px 31px 55px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .main-footer__logo {
    padding: 0;
    border: none;
  }

  .main-footer__social-list {
    padding: 0;
    margin: 0 0 0 25px;
    border: none;
  }

  .main-footer__copyright {
    padding: 0;
  }
}

@media (min-width: @desktop-width) {
  .main-footer__under-wrapper {
    padding: 57px 110px 58px;
  }
}
